.navbar {
    background-color: #0a192f;
    padding: 10px 20px;
    position: fixed;
    
  }
  
  .nav-container {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: fixed;
    width:95%;
    top:2px;
    
  }
  
  /* Navbar menu items */
  .nav-item {
    list-style: none;
    display: inline-block;
    margin-right: 50px;
    text-decoration: none;
    font-size: 18px;
    color: white;
    
   
    
  }
  
  a {
    text-decoration: none;
    color: white;
  }
  
  .navbar-toggler {
    display:none;
    background: none;
    color:#0a192f;
    border: none;
    cursor: pointer;
    padding: 0;
  }
  
  @media (max-width: 768px) {
    .navbar-toggler {
      display: block;
      color:white;
    }
  
    .navbar-menu {
      display: none;
      flex-direction: column;
      align-items:end;
      width: 100%;
      position:fixed;
      top: 50px;
      left: 0;
      background-color:#0a192f;
      padding: 10px 0;
    }
  
    .navbar-menu.open {
      display: flex;
    }
  
    .nav-item {
      margin-bottom: 10px;
    }
  }
  /* ---------------------------------------------------------- */

  .items{
    left:0%;
    display: flex;
    top:50%;
  }
  .text{
    position: absolute;
    left:-100%;
    top:50%;
    transform: translate(10px, -50%);
    white-space: nowrap;
    opacity:0;
    transition: all 0.3s ease;
  }
 
  .text:hover{
    opacity:1;
    left:-80%;
    width: 150px;
    height: 40px;
    font-size: 20px;
    text-align: center;
    align-items: center;
    padding-left: 10px;
    padding-top: 10px;
  }
  .animate{
    display: inline-block;
    position:relative;
    cursor: pointer;
    font-size: 40px;
    transition: all 0.3s ease;
    width: 50px;
    height: 50px;
    text-align: center;
    left:-80%;
    top:110%;
    
  }
  .animate:hover{
    transform: translateX(-10px);
  }
  button{
    background-color: #0a192f;
    border:2px solid white;
  }
  button:hover{
    background-color: #fb0c87;
    border:2px solid #fb0c87;
  }
  .about{
    color:white;
    font-size: 30px;
    font-weight: bold;
    margin-top: 300px;
    text-align: center;
  }
  .pic{
     border-radius: 950px; 
     border-left:2px solid #fb0c87;
     border-right:2px solid #fb0c87 ;
    align-items: center;
    
  }
  .underline{
    text-decoration: underline;
    text-decoration-color: #fb0c87;
    text-decoration-style: solid;
  }
  .grid-container{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    /* grid-gap: 2px; */
  }
  .grid-item1{
    
    padding-top: 10px;
    text-align: center;
    font-size: 30px;
    color:white;
  }
  .grid-item2{
    padding-top:50px;
    text-align: center;
    color:white;
  }
  
  .skill{
    color:white;
    text-align: center;
    padding-top: 100px;
  }
  img{
    width:100px;
    /* padding-left: 20px; */
    transition: all 0.3s ease;
    text-align: center;
    align-items: center;
  }
  img:hover{
    transform: translateX(10px);
  }
  .img-grid {
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-gap: 20px; 
    padding-left: 20px;
    text-align: center;
    align-items: center;
  }
  .contact{
text-align: center;
align-items: center;
